import React, { useEffect, useRef } from "react";

import { useDispatch } from "react-redux";

import styled from "styled-components";
import { Carousel } from "antd";
import { CaretRightOutlined } from "@ant-design/icons";

const StyledTitleBox = styled.div`
  text-align: left;
  font-size: 2rem;
  margin-bottom: 40px;
  > p::after {
    display: inline-block;
    content: "";
    height: 1px;
    background-color: rgba(255, 255, 255, 0.3);
    margin-left: 1rem;
  }
`;

const StyledColor = styled.span`
  color: #59e5c7;
`;

const carouselStyle = {
  height: "45vh",
};

const contentStyle = {
  width: "95%",
  height: "45vh",
  color: "#fff",
  textAlign: "left",
  padding: "10px 15px",
  boxSizing: "border-box",
  marginTop: "30px",
};

const WorkIndex = () => {
  const sectionRef = useRef();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch({
      type: "SAVEOFFSETTOP",
      payload: { id: 1, top: sectionRef.current.offsetTop },
    });
  }, [sectionRef.current]);

  const [dotPosition, setDotPosition] = React.useState("top");

  return (
    <>
      <StyledTitleBox ref={sectionRef}>
        <p>
          <StyledColor>02.</StyledColor> Saemi's TimeLine
        </p>
      </StyledTitleBox>
      <Carousel style={carouselStyle} dotPosition={dotPosition}>
        <div>
          <div style={contentStyle}>
            <p>
              Front-End Developer <StyledColor>@Pet-Friends</StyledColor>
            </p>
            <p>March 2022.08 - Present</p>
            <p>
              <CaretRightOutlined /> Back-End developers, Designer, QA, Font-End
              developers   
            </p>
            <p>
              <CaretRightOutlined /> Vue-bootstrap  UI 
            </p>
            <p>
              <CaretRightOutlined />      
            </p>
            <p>
              <CaretRightOutlined /> React, TS      
            </p>
            <p>
              <CaretRightOutlined /> React-Query   
            </p>
            <p>
              <CaretRightOutlined /> React,Vue  FrameWork   
               
            </p>
          </div>
        </div>
        <div>
          <div style={contentStyle}>
            <p>
              Front-End Developer <StyledColor>@SpringCloud</StyledColor>
            </p>
            <p>March 2021 - 2022.07</p>
            <p>
              <CaretRightOutlined /> Back-End developers, Designer, QA, Font-End
              developers   
            </p>
            <p>
              <CaretRightOutlined /> Vuetify, Material UI  FrameWork
              Library  UI 
            </p>
            <p>
              <CaretRightOutlined /> React-Redux, React-Query   
            </p>
            <p>
              <CaretRightOutlined /> React,Vue  FrameWork   
               
            </p>
          </div>
        </div>
        <div>
          <div style={contentStyle}>
            <p>
              UI/UX  ,  {" "}
              <StyledColor>@</StyledColor>
            </p>
            <p>June 2020 - Nov 2020</p>
            <p>
              <CaretRightOutlined /> Photoshop      
            </p>
            <p>
              <CaretRightOutlined /> HTML, CSS, Jquery    
            </p>
          </div>
        </div>
        <div>
          <div style={contentStyle}>
            <p>
                <StyledColor>@University of Saskatchewan</StyledColor>
            </p>
            <p>July 2013 - June 2018</p>
            <p>
              <CaretRightOutlined /> BA of Psychology 
            </p>
          </div>
        </div>
      </Carousel>
    </>
  );
};

export default WorkIndex;
